import React from 'react';
import {StyleSheet, FlatList, ActivityIndicator, Text, View, TouchableOpacity } from 'react-native';
import Bevitel from "./bevitel";

export default class FetchExample extends React.Component {

  constructor(props){
    super(props);
    this.state =
    { 
      isLoading: true,
      akttema: 1,
      dataSource:[],
      dataSource2:[]
    }
  }

  componentDidMount(){
    return fetch('http://s1.siralycore.hu:25500/tema')
      .then((response) => response.json())
      .then((responseJson) => {

        this.setState({
          isLoading: false,
          dataSource: responseJson,
        }, function(){

        });

        this.kivalaszt(1)

      })
      .catch((error) =>{
        console.error(error);
      });
  }

  kivalaszt = async(szam) =>{
    //alert(szam)
    this.setState({akttema:szam})

    let bemenet = {
      bevitel1:szam
    }

    return fetch('http://s1.siralycore.hu:25500/temauzenet', {
      method: "POST",
      body: JSON.stringify(bemenet),
      headers: {"Content-type": "application/json; charset=UTF-8"}
    })
    .then((response) => response.json())
    .then((responseJson) => {

      this.setState({
        isLoading: false,
        dataSource2: responseJson,
      }, function(){

      });

      //console.log(JSON.stringify(this.state.dataSource2))

    })
    .catch((error) =>{
      console.error(error);
    });
  }



  render(){

    if(this.state.isLoading){
      return(
        <View style={{flex: 1, padding: 20}}>
          <ActivityIndicator/>
        </View>
      )
    }

    return(
      <View style={{flex: 1, paddingTop:20, backgroundColor: "#050025"}}>
        <View>
          <FlatList
            data={this.state.dataSource}
            renderItem={({item}) => 
            <View style={{alignItems: "center", borderColor: "", flex: 2}}>

            <TouchableOpacity style={{backgroundColor: "gray", width: 300, margin: 10, borderRadius: 10}} onPress={async () => this.kivalaszt(item.tema_id)}>
              <Text style={{color:"white",fontSize:20,textAlign:"center",marginTop:15,marginBottom:5}}>{item.tema_nev} </Text>
            </TouchableOpacity>

            </View>
        
          }keyExtractor={({tema_id}, index) => tema_id}
          />
        </View>
{/*------ Beviteli mező --------------------------------------------------------------------------------------- */}

        <Bevitel />

{/*------ Üzenetek -------------------------------------------------------------------------------------------- */}
        <FlatList
          data={this.state.dataSource2}
          renderItem={({item}) => 
          <View style={{borderWidth: 1, borderColor: "black", borderRadius: 10, padding: 5, margin: 5, backgroundColor: "aqua"}}>
            <Text style={{color:"black",fontSize:20}}>{item.uzenet_szoveg} </Text>
            <Text style={{color:"black",fontSize:15}}>{item.uzenet_nev} </Text>
            <Text style={{color:"black",fontSize:12}}>{item.uzenet_datum} </Text>

          </View>
        
        }
        keyExtractor={({uzenet_id}, index) => uzenet_id}
        />
      </View>
    );
  }
}

const styles = StyleSheet.create({
  kekgomb: {
    alignItems: "center",
    backgroundColor: "blue",
    padding: 10,
    width:300,
    marginLeft:"auto",
    marginRight:"auto",
  }
});