import { setStatusBarNetworkActivityIndicatorVisible } from 'expo-status-bar';
import React, { Component } from 'react';
import { Text, TextInput, View, TouchableOpacity } from 'react-native';

export default class Bevitel extends Component {
  constructor(props) {
    super(props);
    this.state = {
        nev: '',
        komment: ''
    };
  }

  render() {
    return (
      <View style={{padding: 10, backgroundColor: "blue", margin: 15, alignItems: "center"}}>
        <Text style={{padding: 10, fontSize: 20, color: "white"}}>
            Név:
        </Text>
        <TextInput
          placeholderTextColor="white"
          style={{height: 40, color: "white", borderColor: "white", borderWidth: 1, padding: 5, width: "100%"}}
          placeholder="Add meg a nevedet."
          onChangeText={(nev) => this.setState({nev})}
          value={this.state.nev}
        />
        <Text style={{padding: 10, fontSize: 20, color: "white"}}>
            Komment:
        </Text>
        <TextInput
          multiline={true}
          numberOfLines={4}
          placeholderTextColor="white"
          style={{height: 100, textAlignVertical: "top", color: "white", borderColor: "white", borderWidth: 1, padding: 5, width: "100%"}}
          placeholder="Írd meg a kommented."
          onChangeText={(komment) => this.setState({komment})}
          value={this.state.komment}
        />
        <TouchableOpacity style={{backgroundColor: "gray", width: 300, margin: 10, borderRadius: 10}}>
            <Text style={{color:"white",fontSize:20, textAlign:"center", padding: 10}}>Felvitel</Text>
          </TouchableOpacity>
      </View>
    );
  }
}